import React from 'react';
import type { FC } from 'react';
import type { UserInfo } from '@wix/cli-auth';
import { Text } from '@wix/cli-ui-kit';
import { Trans } from '@wix/cli-i18n';
import { Unauthenticated } from './Unauthenticated.js';

interface Props {
  userInfo: UserInfo | null;
}

export const WhoamiCommand: FC<Props> = ({ userInfo }) => {
  if (userInfo == null) {
    return <Unauthenticated />;
  }

  return (
    <Trans
      i18nKey="whoami_command.logged_in"
      components={[<Text skin="warning" bold />]}
      values={{ email: userInfo.email }}
    />
  );
};
